.title{
    margin: 0px 0px 20px 0px;
    padding: 0;
    text-align: center;
    color:whitesmoke;
  }
  
  .container{
    
    display: -webkit-flex;
    
    display: flex;
    list-style-type: none;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-flow: wrap row;
            flex-flow: wrap row;
    
    width: 800px;
    padding: 0;
    margin: 0;
  }
  
  .list{
    position: relative;
    color:whitesmoke;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: wrap column;
            flex-flow: wrap column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    
    margin: 10px;
    padding: 0;
  }
  
  .button{
    position: absolute;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    height: 30px;
    background-color: gray;
    border: none;
    color:white;
    border-radius: 5px;
  }
  
  .image{
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  .overlay {
    position: absolute;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
  }
  
   /* img:hover + .button, .button:hover{
    visibility: visible;
  }  */
  
  .list:hover .image {
    opacity: 0.3;
  }
  
  .list:hover .button {
    opacity: 0.8;
  }
  
  .list:hover .overlay {
    opacity: 1;
  }
  
